import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, NavLink } from 'react-router-dom';

import './styles/index.css';
import sun from './assets/sun.svg';


import Content from './components/content/content';


const menus = [
    {
        name: 'home',
        href: '/',
    },
    {
        name: 'about',
        href: '/about',
    },
    {
        name: 'skills',
        href: '/skills',
    },
    {
        name: 'work',
        href: '/work',
    },
    {
        name: 'contact',
        href: '/contact',
    },
];

const Logo = function logoPureComponent(props) {
    return (
        <NavLink className="logo" to={props.href}>
            <img className="logo__img" src={props.src} alt={'Website logo'} />
        </NavLink>
    );
};

const Menu = function menuPureComponent(props) {
    return (
        <li>
            <NavLink className={`nav__item nav__item-${props.index + 1}`} exact to={props.href} activeClassName="nav__item--active">{props.name}</NavLink>
        </li>
    );
}

class Header extends React.Component {

    renderMenu(e, i) {
        return (
            <Menu active={e.active} name={e.name} href={e.href} key={i} index={i}></Menu>
        );
    }

    render() {
        return (
            <header className="header">
                <Logo href={'/'} src={sun}></Logo>
                <nav className="nav">
                    <ul className="nav__ul">
                        {
                            menus.map((e, i) => this.renderMenu(e, i))
                        }
                    </ul>
                </nav>
            </header>
        )
    }
}

class Site extends React.Component {

    render() {
        return (
            <div className="wrapper">
                <Header></Header>
                <Content></Content>
            </div>
        );
    }
}


ReactDOM.render(  (
    <BrowserRouter>
        <Site />
    </BrowserRouter>
), document.getElementById('root'));



