import React from 'react';

import html from './../../../assets/html5.svg';
import css from './../../../assets/css3.svg';
import sass from './../../../assets/Sass_Logo_Color.svg';
import js from './../../../assets/javascript.svg';
import es6 from './../../../assets/es6.svg';
import jq from './../../../assets/jquery-1.svg';
import node from './../../../assets/nodejs-icon.svg';
import angular from './../../../assets/angular-icon-1.svg';
import react from './../../../assets/react-1.svg';
import webpack from './../../../assets/webpack.svg';
import ps from './../../../assets/adobe-photoshop-cs6.svg';
import ai from './../../../assets/adobe-illustrator-cs6.svg';
import git from './../../../assets/git-icon.svg';
import linux from './../../../assets/linux-tux.svg';


const skills = [
    {
        name: 'HTML',
        src: html,
        class: 'html',
    },
    {
        name: 'CSS',
        src: css,
        class: 'css',
    },
    {
        name: 'Sass',
        src: sass,
        class: 'sass',        
    },
    {
        name: 'JavaScript',
        src: js,
        class: 'javascript',
    },
    {
        name: 'ES6',
        src: es6,
        class: 'es6',
    },
    {
        name: 'jQuery',
        src: jq,
        class: 'jq',
    },
    {
        name: 'NodeJS',
        src: node,
        class: 'node',
    },
    {
        name: 'Anguar',
        src: angular,
        class: 'angular',
    },
    {
        name: 'React',
        src: react,
        class: 'react',
    },
    {
        name: 'Webpack',
        src: webpack,
        class: 'webpack',
    },
    {
        name: 'Photoshop',
        src: ps,
        class: 'ps',
    },
    {
        name: 'Illustrator',
        src: ai,
        class: 'illustrator',
    },
    {
        name: 'Git',
        src: git,
        class: 'git',
    },
    {
        name: 'Linux',
        src: linux,
        class: 'linux',
    },
];


class Skills extends React.Component {
    render() {
        return (
            <section className="section skills">
                <h1>Skills</h1>
                <SkillsList></SkillsList>
            </section>
        )
    }
}


class SkillsList extends React.Component {
    render() {
        return (
            <ul className="skills__list">
                { skills.map((e, i) => <SkillsItem skill={e} index={i + 1} key={i}></SkillsItem>) }
            </ul>
        );
    }
}

function SkillsItem(props) {
    return (
        <li className={`skills__item skills__item--${props.index}`}>
            <div className="skills__svg-wrapper">
                <img className={`skills__img ${props.skill.class}`} src={props.skill.src} alt={props.skill.name}/>
            </div>
            <h2 className="skills__title">{props.skill.name}</h2>
        </li>
    );
}

export default Skills;


