import React from 'react';


class About extends React.Component {
    render() {
        return (
            <section className="section about">

                <h1 className="section__title">About me</h1>

                <p className="about__p">
                    I'm web developer with some experience in the field.
                </p>

                <p className="about__p">
                    I'm looking for opportunities to change the stack and try something hardcore like Python and/or C++.
                </p>

                <p className="about__p">
                    Ultimately, my goals are bioinformatics and data science.
                </p>
            </section>
        )
    }
}

export default About;


